
<div>
  <div
    v-show="numSelected !== 0"
    class=""
  >
    <form
      accept-charset="UTF-8"
      method="post"
    >
      <input
        :name="csrfTokenName"
        :value="csrfTokenValue"
        type="hidden"
      >
      <input
        v-for="selectedId in selectedIds"
        :key="selectedId"
        :value="selectedId"
        name="statisticIds[]"
        type="hidden"
      >
      <label class="text-gray-600">{{ numSelected }} statistic<span v-if="numSelected !== 1">s</span>:</label>
      <div class="btngroup inline">
        <div
          class="btn menubtn"
          data-icon="settings"
        />
        <div
          class="menu"
          data-align="right"
        >
          <ul>
            <li>
              <a
                class="formsubmit"
                data-action="retour/statistics/delete-statistics"
              >Delete</a>
            </li>
          </ul>
        </div>
      </div>
    </form>
  </div>
  <vuetable-filter-bar
    v-show="numSelected === 0"
    :initial-filter-text="filterText"
  />
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info ref="paginationInfoTop" />

    <div class="floated left pl-3 pt-3 text-gray-600">
      <div class="select">
        <select
          v-model="retourHandled"
          class="fieldtoggle"
          data-target-prefix="retour-handled-"
          name="retourHandled"
        >
          <option
            selected
            value="all"
          >
            All
          </option>
          <option value="handled">
            Handled
          </option>
          <option value="nothandled">
            Not Handled
          </option>
        </select>
      </div>
    </div>

    <vuetable-pagination
      ref="paginationTop"
      @vuetable-pagination:change-page="onChangePage"
    />

    <div class="floated left vuetable-pagination-info py-3">
      <div class="inline pl-3 text-gray-600">
        Per-page:
      </div>
      <div class="inline pl-3 text-gray-600">
        <div class="select">
          <select
            v-model="perPage"
            class="fieldtoggle"
            data-target-prefix="per-page-"
            name="perPage"
          >
            <option
              selected
              value="20"
            >
              20
            </option>
            <option value="50">
              50
            </option>
            <option value="100">
              100
            </option>
            <option value="500">
              500
            </option>
          </select>
        </div>
      </div>
    </div>
  </div>
  <vuetable
    ref="vuetable"
    :api-url="apiUrl"
    :append-params="moreParams"
    :css="css"
    :fields="fields"
    :per-page="perPage"
    :sort-order="sortOrder"
    @vuetable:pagination-data="onPaginationData"
  />
  <div class="vuetable-pagination clearafter">
    <vuetable-pagination-info ref="paginationInfo" />
    <vuetable-pagination
      ref="pagination"
      @vuetable-pagination:change-page="onChangePage"
    />
  </div>
</div>
